"use client"

import { useEffect } from "react"
import { useMap } from "react-leaflet"
import L from "leaflet"
import "leaflet.heat"
import { parseWKT, getLatestDataPerDevice } from "@/lib/heatmap"
import airData from "@/app/stakeholder/data.json"

interface HeatmapLayerProps {
  location: "semarang" | "yogyakarta"
}

export function HeatmapLayer({ location }: HeatmapLayerProps) {
  const map = useMap()

  useEffect(() => {
    // Filter data sesuai lokasi
    const filtered = airData // tanpa filter lokasi, untuk uji coba

    // Ambil data terbaru per device
    const latestPoints = getLatestDataPerDevice(filtered)

    // Siapkan data untuk heatmap
    const heatData = latestPoints.map((item) => {
        const [lat, lng] = parseWKT(item.location)
        return [lat, lng, item.ispu_realtime] as [number, number, number]
    })

    console.log(" Heatmap data:", heatData)
    console.log(" Latest device data:", latestPoints)

    // Buat heat layer
    const heatLayer = L.heatLayer(heatData, {
        radius: 35,
        blur: 15,
        maxZoom: 17,
        max: 300, // ini kunci: ISPU maksimum
        gradient: {
          0: "#22c55e",     // green-500  Baik
          50: "#eab308",    // yellow-500  Sedang
          100: "#f97316",   // orange-500  Tidak Sehat
          200: "#ef4444",   // red-500  Sangat Tidak Sehat
          300: "#9333ea",   // purple-600  Berbahaya
        },
      })
      

    // Tambahkan ke map
    heatLayer.addTo(map)

    // Bersihkan layer saat location berubah
    return () => {
      map.removeLayer(heatLayer)
    }
  }, [location, map])

  return null
}
